import { MEmitter } from 'proto-ui.vusion';

export const UResizeTableColumn = {
    name: 'u-resize-table-column',
    parentName: 'u-resize-table',
    mixins: [MEmitter],
    props: {
        move: {
            type: Boolean,
            default: true,
        },
        title: String,
        label: String,
        width: [Number, String],
        //  
        minWidth: {
            type: [Number, String],
            default: 46,
        },
        filterMaxWidth: [String, Number], //   
        headSelection: { type: Boolean, default: true }, // checkbox
        ellipsis: { type: Boolean, default: false },
        type: { type: String, default: 'normal' }, // selection,filter,sortable,normal,time,expand 6
        timeFormat: { type: String, default: 'YYYY-MM-DD HH:mm:ss' }, // 
        options: Array,
        value: [String, Number, Boolean],
        field: { type: String, default: 'text' },
        filter: Function, // 
        icon: String, // 
        iconContent: String,
        iconPlacement: {
            type: String,
            default: 'bottom',
        },
        expandIcon: { type: String, default: 'right-down', validator: (value) => ['up-down', 'right-down'].includes(value) },
        expandStrict: { type: Boolean, default: false }, // expand labelicon
        expandLabel: String, // expand
        expandClass: String, // expandicon
        defaultText: { type: String, default: '-' }, // 
        sortMethod: Function,
        sortRemoteMethod: Function,
        filterMethod: Function,
        visible: { type: Boolean, default: true }, // 
    },
    created() {
        this.dispatch(this.$options.parentName, 'add-item-vm', this);
    },
    data() {
        return {
            currentWidth: this.getWidth(),
            copyWidth: this.getWidth(), //  
            digitWidth: undefined, // resizetable mounted
            parentVM: null,
            selectValue: this.value,
            row: {},
        };
    },
    computed: {
        currentEllipsis() {
            return this.parentVM.ellipsis || this.ellipsis;
        },
        currentMinWidth() {
            if ((this.type === 'sortable' || this.type === 'filter' || this.icon) && this.minWidth === 46)
                return 66;
            else
                return parseFloat(this.minWidth);
        },
    },
    watch: {
        width(newValue) {
            this.currentWidth = this.digitWidth = newValue;
            // resizeTable
            this.parentVM.handleSize();
        },
        value(newValue) {
            this.selectValue = newValue;
        },
        selectValue(newValue) {
            this.$emit('update:value', newValue);
        },
        type() {
            // v-if
            this.currentWidth = this.copyWidth = this.getWidth();
        },
    },
    methods: {
        getWidth(value) {
            if (this.type === 'selection' && !this.title)
                return value || this.width || 46;
            else if (this.type === 'expand')
                return value || this.width || 50;
            else
                return value || this.width;
        },
    },
    destroyed() {
        this.dispatch(this.$options.parentName, 'remove-item-vm', this);
    },
};

export default UResizeTableColumn;



